import React, { Component, Fragment } from "react";
import "./Coupons.css";
import Coupon from "./Coupon/Coupon";
class Coupons extends Component {
  render() {
    return (
      <Fragment>
        <Coupon></Coupon>
        <Coupon></Coupon>
      </Fragment>
    );
  }
}

export default Coupons;
