import React, { Component, Fragment } from "react";
import "./Review.css";
import { MDBIcon } from "mdbreact";
import FittedImage from "react-fitted-image";

class Review extends Component {
  render() {
    return (
      <Fragment>
        <div class="review">
          <FittedImage
            fit="contain"
            src="https://www.dhresource.com/fc/s009/homepage/20191217/W1.jpg"
            alt="Avatar"
            style={{ maxHeight: "195px", maxWidth: "100%" }}
          />
          <div class="container" style={{ backgroundColor: "white" }}>
            <div style={{ marginTop: "10px" }}>
              <MDBIcon icon="star" />
              <MDBIcon icon="star" />
              <MDBIcon icon="star" />
              <MDBIcon icon="star" />
              <MDBIcon far icon="star" />
            </div>

            <p>
              "Lorem ipsum dolor sit amet, et nam pertinax gloriatur. Sea te
              minim soleat senserit, ex quo luptatum tacimates voluptatum,
              salutandi delicatissimi eam ea. In sed nullam laboramus
              appellantur, mei ei omnis dolorem mnesarchum."
            </p>
          </div>
          <div class="container">
            <button className="btn btn-primary">Edit</button>
            <button className="btn btn-default">Remove</button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Review;
