import React, { Component, Fragment } from "react";
import "./Profile.css";

class Profile extends Component {
  showtab(cityName) {
    console.log("sds");
    var i, tabcontent, tablinks;
    tabcontent = document.getElementsByClassName("tabcontent");
    for (i = 0; i < tabcontent.length; i++) {
      tabcontent[i].style.display = "none";
    }
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(" active", "");
    }
    document.getElementById(cityName).style.display = "block";
  }

  render() {
    return (
      <Fragment>
        <div>
          <div className="tab" style={{ marginTop: "25px" }}>
            <button
              className="tablinks active"
              onClick={() => this.showtab("personal")}
            >
              Personal Information
            </button>
          </div>

          <div
            id="personal"
            style={{ display: "block" }}
            className="tabcontent"
          >
            <div className="row itemtable">
              <div className="col-md-6">
                <div className="row item">
                  <div className="col-md-6">Name</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">Gender</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">Birthday</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">Email</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">Mobile</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
              </div>
              <div className="col-md-6">
                <div className="row item">
                  <div className="col-md-6">Telephone</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">Country/State</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">Fax</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
                <div className="row item">
                  <div className="col-md-6">GST Tax No</div>
                  <div className="col-md-6">: Mr.A</div>
                </div>
              </div>
            </div>

            <button className="btn btn-primary">Edit</button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Profile;
