import React, { Component, Fragment } from "react";
import Navbar from "../../components/Navbar/Navbar";
import Multislider from "../../components/Multislider/Multislider";
import Footer from "../../components/Footer/Footer";
import "./User.css";
import ScrollUpButton from "react-scroll-up-button"; //Add this line Here
import queryString from "query-string";
import { MDBListGroup, MDBListGroupItem, MDBContainer } from "mdbreact";
import { Link } from "react-router-dom";
import Productsadded from "../../components/Productsadded/Productsadded";
import Profile from "../../components/Profile/Profile";
import Coupons from "../../components/Coupons/Coupons";
import Reviews from "../../components/Reviews/Reviews";

class User extends Component {
  state = {};
  showtab(cityName) {
    console.log("sds");
    var i, tabcontent, tablinks;
    tabcontent = document.getElementsByClassName("tabcontent");
    for (i = 0; i < tabcontent.length; i++) {
      tabcontent[i].style.display = "none";
    }
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(" active", "");
    }
    document.getElementById(cityName).style.display = "block";
  }

  render() {
    const path = queryString.parse(this.props.location.search, {
      ignoreQueryPrefix: true
    }).path;

    const activestyle = {
      color: "orange"
    };
    return (
      <Fragment>
        <ScrollUpButton />

        <Navbar></Navbar>

        <MDBContainer fluid className="home">
          <div style={{ marginTop: "25px" }}>
            <div className="row">
              <div className="col-md-3">
                <div>
                  <MDBContainer>
                    <h6>Shortcuts</h6>
                    <MDBListGroup style={{ width: "100%" }}>
                      <MDBListGroupItem>
                        {path === "profile" ? (
                          <Link
                            to="/user?path=profile"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My Profile
                          </Link>
                        ) : (
                          <Link to="/user?path=profile" className="Link">
                            My Profile
                          </Link>
                        )}
                      </MDBListGroupItem>
                      <MDBListGroupItem>
                        {path === "orders" ? (
                          <Link
                            to="/user?path=orders"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My Orders
                          </Link>
                        ) : (
                          <Link to="/user?path=orders" className="Link">
                            My Orders
                          </Link>
                        )}
                      </MDBListGroupItem>
                      <MDBListGroupItem>
                        {path === "coupons" ? (
                          <Link
                            to="/user?path=coupons"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My Coupons
                          </Link>
                        ) : (
                          <Link to="/user?path=coupons" className="Link">
                            My Coupons
                          </Link>
                        )}
                      </MDBListGroupItem>
                      <MDBListGroupItem>
                        {path === "reviews" ? (
                          <Link
                            to="/user?path=reviews"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My Reviews
                          </Link>
                        ) : (
                          <Link to="/user?path=reviews" className="Link">
                            My Reviews
                          </Link>
                        )}
                      </MDBListGroupItem>
                      <MDBListGroupItem>
                        {path === "history" ? (
                          <Link
                            to="/user?path=history"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My History
                          </Link>
                        ) : (
                          <Link to="/user?path=history" className="Link">
                            My History
                          </Link>
                        )}
                      </MDBListGroupItem>
                      <MDBListGroupItem>
                        {path === "cart" ? (
                          <Link
                            to="/user?path=cart"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My Cart
                          </Link>
                        ) : (
                          <Link to="/user?path=cart" className="Link">
                            My Cart
                          </Link>
                        )}
                      </MDBListGroupItem>
                      <MDBListGroupItem>
                        {path === "wishlist" ? (
                          <Link
                            to="/user?path=wishlist"
                            className="Link"
                            style={{ ...activestyle }}
                          >
                            My Wishlist
                          </Link>
                        ) : (
                          <Link to="/user?path=wishlist" className="Link">
                            My Wishlist
                          </Link>
                        )}
                      </MDBListGroupItem>
                    </MDBListGroup>
                  </MDBContainer>
                </div>
              </div>
              <div className="col-md-9">
                {path === "profile" ? <Profile></Profile> : null}
                {path === "orders" ? (
                  <div style={{ margin: "20px" }}>
                    <Productsadded type="history"></Productsadded>
                  </div>
                ) : null}
                {path === "history" ? (
                  <div style={{ margin: "20px" }}>
                    <Productsadded type="history"></Productsadded>
                  </div>
                ) : null}
                {path === "wishlist" ? (
                  <div style={{ margin: "20px" }}>
                    <Productsadded type="wishlist"></Productsadded>
                  </div>
                ) : null}
                {path === "cart" ? (
                  <div style={{ margin: "20px" }}>
                    <Productsadded type="cart"></Productsadded>
                  </div>
                ) : null}
                {path === "coupons" ? (
                  <div style={{ margin: "20px" }}>
                    <Coupons></Coupons>
                  </div>
                ) : null}
                {path === "reviews" ? (
                  <div style={{ margin: "20px" }}>
                    <Reviews></Reviews>
                  </div>
                ) : null}
              </div>
            </div>

            {/* Customers Who Bought This Item Also Bought */}
            <div style={{ marginTop: "100px" }}>
              <h4 style={{ margin: "5px" }}>Hot-sale Products</h4>

              <div style={{ background: "white" }}>
                <Multislider
                  sponsers={false}
                  picheight={"150px"}
                  height={"250px"}
                  width={"200px"}
                  numbers={4}
                ></Multislider>
              </div>
            </div>
            {/* Similar Items */}
            <div style={{ marginTop: "100px" }}>
              <h4 style={{ margin: "5px" }}>
                Other items you might be interested in
              </h4>

              <div style={{ background: "white" }}>
                <Multislider
                  sponsers={false}
                  picheight={"150px"}
                  height={"250px"}
                  width={"200px"}
                  numbers={4}
                ></Multislider>
              </div>
            </div>
          </div>
        </MDBContainer>

        <Footer></Footer>
      </Fragment>
    );
  }
}

export default User;
